.preview__header {
  margin-top: 50px;
  display: flex;
  width: 100%;
  min-height: 70px;
  /* max-height: 200px; */
}

.preview__header .preview__header-1 {
  text-align: center;
  width: 30%;
}
.preview__header .preview__header-2 {
  width: 70%;
}
.preview__header-avatar {
  width: 200px;
  height: auto;
}
.preview__header-name {
  top: 200px;
  width: 100%;
  height: 50px;
  text-align: left;
  font-size: 32px;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #ddd;
}

.preview__body {
  display: flex;
  min-height: 800px;
}
.preview__body-1 {
  width: 30%;
}
.preview__body-1 p {
  padding-left: 5px;
}
.preview__body-2 {
  width: 70%;
}
.preview__body-2 li {
  list-style-type: none;
}
.preview__body-experienceText {
  font-size: 32px;
  font-weight: 700;
  width: 100%;
  text-transform: uppercase;
  position: relative;
}
.preview__body-experienceText::after {
  content: "";
  position: absolute;
  top: 1.5em;
  left: 0;
  right: 0;
  height: 0.5em;
  width: 100%;
  border-top: 2px solid black;
  z-index: -1;
}

.preview__body-experience-date {
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 20px;
  text-align: right;
}

.preview__body-experience-title span {
  font-weight: 700;
  font-size: 24px;
}
.preview__body-experience-title p:nth-child(3) {
  font-weight: 700;
  font-size: 16px;
}
.preview__body-experience-title p:last-child {
  position: relative;
}

.preview__body-educationText {
  font-size: 32px;
  font-weight: 700;
  width: 100%;
  text-transform: uppercase;
  position: relative;
}
.preview__body-educationText::after {
  content: "";
  position: absolute;
  top: 1.5em;
  left: 0;
  right: 0;
  height: 0.5em;
  width: 100%;
  border-top: 2px solid black;
  z-index: -1;
}
.preview__body-education-date {
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 20px;
  text-align: right;
}
.preview__body-education-summary span {
  font-size: 16px;
  font-weight: 700;
}

